@import 'colors';
@import 'fonts';

html {
	font-size: 62.5%; }
body {
	@include f-regular;
	color: $black; }
p, h1 {
	margin: 0; }

h1 {
	@include f-bold; }

.border {
	border: 1px solid black;
	&-r {
		border: 1px solid red; } }

.glob-wrap {}

.section {
	position: relative;
	padding: 6rem 3.2rem;
	overflow: hidden;
	&__title {
		@include f-bold;
		color: $black;
		font-weight: bold;
		font-size: 6rem;
		line-height: 8rem;
		width: min-content; }
	&__chat-ic {
		position: absolute; } }

.spec-border {
	border: 1rem solid transparent;
	border-image: url('../img/spec-border-square.svg') 30 round;
	border-image-outset: 0.6rem;
	box-shadow: 0 0 0 2px $black;
	position: relative;
	&::before {
		content: '';
		display: block;
		height: 2.2rem;
		width: 2.2rem;
		background: url("../img/mouse-icon.svg");
		background-size: cover;
		position: absolute;
		bottom: -3rem;
		right: -3rem; } }


@import 'sections/principles';
@import 'sections/lets-talk';
@import 'sections/footer';


